.home-wrapper {
  @include flex(row);
  height: 100vh;

  h2 {
    @media (max-width: 768px) {
      font-size: 1.5rem;
    }
    @media (max-width: 425px) {
      font-size: 1rem;
    }
  }

  @media (max-width: $mobileNavBar) {
    margin-top: -3rem;
  }

  .home-logo {
    @include bg-img("../../assets/images/freelancing99.png", 300, 300);
    margin: 2.4rem auto;
  }

  .home-wall-logo {
    width: 128px;
    height: 128px;
    position: absolute;
    top: 0;
    right: 0;

    @media (max-width: 545px) {
      width: 80px;
      height: 80px;
    }
    @media (max-width: 320px) {
      display: none;
    }
  }
}

.job-role {
  font-family: Arial, Helvetica, sans-serif;
  @extend .fancy-txt;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  text-decoration: none;
}